.marked-card {
  background: linear-gradient(360deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0) 98.83%), $header_color;

  box-shadow: 0px 13px 66px rgba($secondary, 0.06);
  border-radius: 20px;
  padding-bottom: 26px;

  margin-bottom: 29px; }

.marked-card__img {
  width: 100%;
  height: 280px;
  object-fit: contain;
  position: relative;
  padding-bottom: 30px;

  @include r($mobile) {
    height: 200px; } }



.marked-card__title {
  font-family: $second-font-family;
  font-weight: 700;
  font-size: 24px;
  line-height: 33 / 24 + em;
  color: $secondary;
  @include r($tablet) {
    font-size: 20px; } }

.marked-card__img-wrapper {
  position: relative;
  margin-bottom: 23px;

  &:after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 1px;
    bottom: 0;
    background-color: rgba($secondary, 0.1); } }
