.item-card__img {
  background: $header_color;
  box-shadow: 0 13px 66px rgba($secondary, 0.06);
  border-radius: 20px;
  padding: 11px;
  margin-bottom: 16px;
  object-fit: cover;
  height: 221px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 100%;
    height: auto;
    object-fit: scale-down; } }

.item-card__title {
  font-family: $second-font-family;
  font-weight: 600;
  font-size: 18px;
  line-height: 25 / 18 + em;
  color: $secondary; }


