.location {
  height: 610px;
  position: relative;
  margin-bottom: 80px;
  @include r($desktop) {
    height: auto; } }

.location__map {
  position: absolute;
  right: 0;
  width: 100%;
  left: 50%;
  height: 610px;
  @include r($desktop) {
    display: none; } }

.location__content {
  width: 40%;
  .location__map {
    position: static;
    height: 350px;
    display: none;
    @include r($desktop) {
      width: 100%;
      display: block; }
    @include r($mobile) {
      height: 250px; } }
  @include r($desktop) {
    width: 100%;
    .location__content {
      display: block; } } }

.location__title {
  max-width: 512px;
  width: 100%;
  margin-bottom: 27px;

  @include r($desktop) {
    max-width: 100%; } }

.location__card {
  margin-bottom: 20px; }

.location__card-icon {
  width: 70px;
  height: 70px;
  border-radius: 20px;
  box-shadow: 0px 13px 66px rgba($secondary, 0.06);
  background-color: $main;
  @include r($tablet) {
    width: 60px;
    height: 60px; }

  svg {
    fill: $secondary;
    display: flex;
    align-items: center;
    justify-content: center; }

  .pin {
    width: 22px;
    height: 30px; }
  .clock {
    width: 26px;
    height: 35px; }
  .email {
    width: 30px;
    height: 30px; } }

.location__card-text {
  margin-left: 16px;
  font-family: $second-font-family;
  font-weight: 600;
  font-size: 18px;
  line-height: 27 / 18 + em;
  color: $secondary; }
