.deliveries {
  background-image: url("/img/deliveries/bg.png");
  padding-top: 72px;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 80px;
  margin-bottom: 80px;
  @include r($tablet) {
    padding: 40px 0;
    margin-bottom: 40px; } }

.deliveries__title {
  margin-bottom: 40px; }

.deliveries__cards {
  @include r($tablet) {
    flex-direction: column;
    .delivery-card {
      margin-bottom: 30px; } } }
