.header {
  background-color: $header_color;
  position: absolute;
  left: 0;
  right: 0;
  top: 0; }
.header__logo {
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 48 / 40 + em;
  padding: 32px 0;
  @include r($tablet) {
    padding: 12px 0; }

  &:hover {
    color: inherit; }

  span {
    color: $main; } }

.header__nav {
  @include r($wide) {
    display: none; }
  a {
    margin-right: 56px;
    &:last-child {
      margin-right: 0; } } }

.header__link {
  font-family: $second-font-family;
  font-weight: 600;
  font-size: 16px;
  line-height: 22 / 16 + em;

  position: relative;

  &:hover {
    color: $main;
    transition: color .2s ease;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -4px;
      right: 0;
      height: 2px;
      background-color: $main; } } }


.header__buttons {
  @include r($wide) {
    display: none !important; }

  & .call-button:nth-child(2) {
    margin-left: 12px;
    margin-right: 40px; } }

.select {
  position: relative;

  select {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    background: transparent;
    border: none;
    padding-right: 17px;
    z-index: 2;
    cursor: pointer; }

  option {
    font-weight: 400;
    font-size: 16px;
    line-height: 22 / 16 + em;
    color: $secondary; }


  .select__icon {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 0;
    svg {
      width: 11px;
      height: 6px; } } }

.header__mobile-button {
  @include r($wide) {
    width: 40px;
    height: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    cursor: pointer;

    span {
      width: 100%;
      height: 2px;
      display: block;
      background-color: $secondary;
      border-radius: 2px; }
    &:hover {
      span {
        transition: background-color .2s ease;
        background-color: $main; } } } }

.mobile-menu {
  display: none;
  position: fixed;
  background-color: $header_color;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 12;
  overflow: auto;
  &.open {
    display: flex;
    justify-content: center; }
  .mobile-menu__inner {
    margin: auto;
    padding: 15px 0; }

  .header__buttons {
    display: block !important;
    .call-button {
      width: 100%; }
    .call-button:nth-child(2) {
      margin: 0;
      margin-top: 10px;
      margin-bottom: 20px; } }

  .header__nav {
    .header__link {
      margin-bottom: 20px;
      margin-right: 0; } }

  .mobile-menu__close {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 15px;
    top: 30px;
    span {
      width: 100%;
      border-radius: 2px;
      height: 2px;
      background-color: $secondary;
      display: block;
      transform: rotate(-45deg);
      &:nth-child(2) {
        transform: rotate(45deg); } } } }
