.item-faq__title {
  margin-bottom: 32px; }

.item-faq__accordion {
  background: $header_color;
  box-shadow: 0px 13px 66px rgba(39, 50, 56, 0.06);
  border-radius: 20px;
  margin-bottom: 30px;

  &.open {
    .accordion_arrow {
      transform: rotate(0deg); }
    .item-faq__accordion-top {
      border-bottom: 1px solid rgba(#000, 0.1); }
    .item-faq__accordion-content {
      display: block; } } }

.item-faq__accordion-top {
  cursor: pointer;
  padding: 23px 20px 26px 21px;
  @include r($tablet) {
    padding: 13px 20px 16px 21px; }

  .accordion_arrow {
    width: 12px;
    height: 12px;
    fill: $main;
    transform: rotate(180deg); } }


.item-faq__accordion-content {
  padding: 23px;
  display: none;
  @include r($tablet) {
    padding: 13px; } }

.item-faq__accordions {
  margin-top: 33px;
  padding-bottom:  80px;
  border-bottom: 1px solid rgba(#000, 0.1);
  @include r($tablet) {
    padding-bottom: 40px; } }
