.call-button {
  background-color: $main;
  border-radius: 20px;

  padding: 10px 14px;

  height: 70px;
  @include r($tablet) {
    height: 50px; }

  .call-button__icon {
    position: relative;
    padding-right: 12px;
    &::before {
      content: "";
      position: absolute;
      right: 0;
      display: block;
      bottom: 0;
      top: 0;
      width: 1px;
      background: rgba($secondary, 0.10); } }

  svg {
    width: 24px;
    height: 24px;
    fill: $secondary; }

  .call-button__content {
    padding-left: 12px;
    text-align: left; }

  &:hover {
    background-color: rgba($main, 0.8);
    transition: background-color .2s ease; }

  .title {
    font-family: $second-font-family;
    font-weight: 400;
    font-size: 16px;
    line-height: 22 / 16 + em;
    color: $secondary;
    @include r($tablet) {
      font-size: 14px; } }
  .phone {
    font-family: $second-font-family;
    font-weight: 700;
    font-size: 18px;
    line-height: 25 / 18 + em;
    color: $secondary;
    @include r($tablet) {
      font-size: 16px; } } }


