
* {
    box-sizing: border-box;
    outline: none; }

body, html {
    background: white;
    color: $fontColor;
    font-family: $font-family !important;
    font-size: 16px;
    min-width: 320px;
    max-width: 100%;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
.page-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
    max-width: 1920px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden; }
.container {
    max-width: 1505px !important;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include r($wide) {
        max-width: 960px !important;
        padding-left: 15px;
        padding-right: 15px; }
    @include r($desktop) {
        max-width: 720px  !important; }
    @include r($tablet) {
        max-width: 540px !important; }
    @include r($mobile) {
        max-width: 100%; } }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
    padding: 0;
    margin: 0; }
a {
    color: $fontColor;
    text-decoration: none;
    font-family: $font-family;
    font-size: 18px;
    font-weight: 400;
    &:hover, &:focus {
        text-decoration: none; } }
button {
    padding: 0;
    border: 0;
    box-shadow: none;
    background: none;
    cursor: pointer;
    &:focus {
        outline: none; } }
ul {
    list-style: none; }
ol {
    list-style: decimal; }
img.svg {
    display: none; }
.swiper-button-next, .swiper-button-prev {
    transform: none;
    margin: 0;
    padding: 0;
    top: auto;
    bottom: auto;
    left: auto;
    right: auto;
    background: none;
    color: transparent;
    &::after {
        display: none; } }

.title {
  font-weight: 600;
  font-size: 50px;
  line-height: 60 / 50 + em;
  color: $secondary;
  @include r($tablet) {
    font-size: 30px; } }

.button {
  background: $main;
  box-shadow: 0px 13px 66px rgba($secondary, 0.06);
  border-radius: 20px;
  min-height: 70px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 22 / 16 + em;
  color: $secondary;
  justify-content: center;

  @include r($tablet) {
    min-height: 50px;
    padding: 5px 20px; }
  &:hover {
    transition: background-color .2s ease;
    color: $secondary;
    background-color: rgba($main, 0.8); } }

.button-outlined {
  border: 1px solid $main;
  min-height: 70px;
  font-family: $second-font-family;
  font-weight: 700;
  font-size: 16px;
  line-height: 22 / 16 + em;
  color: $main;
  display: inline-block;
  padding: 0 20px;
  border-radius: 20px;
  cursor: pointer;

  @include r($tablet) {
    min-height: 50px; }

  &:hover {
    transition: border-color .2s ease;
    border-color: rgba($main, .8); } }

.text {
  font-family: $second-font-family;
  font-weight: 400;
  font-size: 24px;
  line-height: 33 / 24 + em;
  text-align: left;
  color: $secondary;
  @include r($tablet) {
    font-size: 20px; } }
