.footer {
  background-color: $secondary;
  .container {
    padding-top: 70px; }
  .header__logo {
    color: $header_color;
    margin-bottom: 16px;
    padding: 0;
    &:hover {
      color: $header_color !important; } } }

.footer__subtitle {
  margin-bottom: 76px;
  color: rgba($header_color, 0.6);
  font-size: 18px;
  @include r($tablet) {
    font-size: 15px;
    margin-bottom: 30px; } }

.cities {
  margin-bottom: 58px;
  @include r($desktop) {
    margin-bottom: 28px; } }

.footer__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 25 / 18 + em;
  @include r($tablet) {
    font-size: 15px; }
  font-family: $second-font-family;
  color: $header_color;
  margin-bottom: 22px;

  &:hover {
    color: $main; } }

.copyright {
  padding-bottom: 17px;
  padding-top: 20px;
  border-top: 1px solid rgba($header-color, 0.1);
  .footer__subtitle {
    margin-bottom: 0; } }

.arrow {
  position: absolute;
  right: 0;
  top: 0;
  transform: translateY(-50%);
  width: 60px;
  height: 60px;
  background-color: $main;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  z-index: 10;
  cursor: pointer;

  svg {
    width: 17px;
    height: 18px;
    fill: $secondary; }

  &:hover {
    background-color: $secondary;
    svg {
      fill: $header-color; } } }

